import axios from 'axios'
import api from '../utils/api';
import Store from '../store'
const axio = axios.create({
    baseURL: process.env.BASE_API, // nodebaseURL
    timeout: 15000, // 
     //postformdata// axiosheaderContent-Type'application/json;charset=UTF-8',json
     // headers: { 
     // "Content-Type": "application/x-www-form-urlencoded"
     // },
    withCredentials: true // cookie
})
// http request  
axio.interceptors.request.use(
    config => {
        let reqUrl = api.serverDevUrl + config.url
        if (/sandbox.tiejin/.test(window.location.href)) {
            reqUrl = api.serverDevUrl + config.url;
        } else if (/tiejin/.test(window.location.href)) {
            reqUrl = api.serverUrl + config.url;
        }
        config.url = reqUrl;
        if (config.url.indexOf('/command/closer_sys.login') === -1 && config.url.indexOf('/command/closer_sys.register') === -1) {
            config.headers.Authorization = "CloserSysAuth " + Store.state.authUser.token;
        }
        return config;

    },
    err => {
        return Promise.reject(err).catch(err);
    });
// http response  
axio.interceptors.response.use(
    response => {
        return response;
    },
    (err) => {
        if (err && err.response) {
            switch (err.response.status) {
                case 400:
                    err.message = ''
                    break

                case 401:
                    err.message = ''
                    router.push({
                        name: 'worldcupIndex'
                    })
                    break

                case 403:
                    err.message = ''
                    router.push({
                        name: 'worldcupIndex'
                    })
                    break

                case 404:
                    err.message = `: ${err.response.config.url}`
                    break

                case 408:
                    err.message = ''
                    break

                case 500:
                    err.message = ''
                    break

                case 501:
                    err.message = ''
                    break

                case 502:
                    err.message = ''
                    break

                case 503:
                    err.message = ''
                    break

                case 504:
                    err.message = ''
                    break

                case 505:
                    err.message = 'HTTP'
                    break

                default:
            }
        } else {
            err.message = ''
        }
        if (err && err.response && err.response.data && err.response.data.message) {
            console.warn(err.response.data.message)
        } else {
            console.warn(err.message)
        }
        return Promise.reject(err).catch(err)
    });
export default axio